"use client";

import { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import { Filter, Star, ShoppingCart } from "lucide-react";

import { Header } from "@/components/header";
import { Footer } from "@/components/footer";
import { HeroCarousel } from "@/components/hero-carousel";
import { WhatsAppButton } from "@/components/whatsapp-button";

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105";

// ===== Tipos da API =====
interface CategoryApi {
  id: string;
  name: string;
  description: string | null;
  isActive: boolean;
  createdAt: string;
}

interface SizeApi {
  id: number;
  name: string;
  isActive: boolean;
  createdAt: string;
}

interface ColorApi {
  id: number;
  name: string;
  hexCode: string | null;
  isActive: boolean;
  createdAt: string;
}

interface ProductImageApi {
  id: string;
  productId: string;
  url: string;
  altText: string | null;
  isPrimary: boolean;
  displayOrder: number;
  createdAt: string;
}

interface ProductApi {
  id: string;
  categoryId: string;
  name: string;
  description: string | null;
  price: number;
  promotionalPrice: number | null;
  sizeIds: number[];
  colorIds: number[];
  isPersonalizable: boolean;
  isActive: boolean;
  createdAt: string;
  images: ProductImageApi[];
}

// ===== Modelos de tela =====
interface Category {
  id: string;
  name: string;
  slug: string;
}

interface Size {
  id: number;
  name: string;
}

interface Color {
  id: number;
  name: string;
  hex: string | null;
}

interface Product {
  id: string;
  categoryId: string;
  name: string;
  price: number;
  promotionalPrice?: number | null;
  imageUrl: string | null;
  sizeIds: number[];
  colorIds: number[];
  createdAt: string;
}

// Helper para slug
const generateSlug = (nome: string) =>
  nome
    .toLowerCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/(^-|-$)/g, "");

// Faixas de preço (mesmas da ProductsPage)
const PRICE_RANGES = [
  "Todas",
  "R$ 0-100",
  "R$ 100-300",
  "R$ 300-500",
  "R$ 500+",
];

const formatCurrency = (value: number) =>
  value.toLocaleString("pt-BR", {
    style: "currency",
    currency: "BRL",
  });

export default function Home() {
  const [categories, setCategories] = useState<Category[]>([]);
  const [sizes, setSizes] = useState<Size[]>([]);
  const [colors, setColors] = useState<Color[]>([]);
  const [products, setProducts] = useState<Product[]>([]);

  const [loadingSidebar, setLoadingSidebar] = useState(true);
  const [loadingProducts, setLoadingProducts] = useState(true);

  // ESTADOS DO FILTRO (iguais da ProductsPage)
  const [selectedCategoryId, setSelectedCategoryId] = useState<string>("Todos");
  const [selectedPrice, setSelectedPrice] = useState<string>("Todas");
  const [sortBy, setSortBy] = useState<
    "popular" | "newest" | "price-low" | "price-high"
  >("popular");

  const [selectedSizeIds, setSelectedSizeIds] = useState<number[]>([]);
  const [selectedColorIds, setSelectedColorIds] = useState<number[]>([]);
  const [showAllSizes, setShowAllSizes] = useState(false);
  const [showAllColors, setShowAllColors] = useState(false);

  // ===== Fetch categorias, tamanhos, cores e produtos =====
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoadingSidebar(true);
        setLoadingProducts(true);

        const [catRes, sizeRes, colorRes, productRes] = await Promise.all([
          fetch(`${API_BASE_URL}/api/admin/categories`, { cache: "no-store" }),
          fetch(`${API_BASE_URL}/api/admin/sizes`, { cache: "no-store" }),
          fetch(`${API_BASE_URL}/api/admin/colors`, { cache: "no-store" }),
          fetch(`${API_BASE_URL}/api/admin/products`, { cache: "no-store" }),
        ]);

        // Categorias
        if (catRes.ok) {
          const data: CategoryApi[] = await catRes.json();
          const mapped: Category[] = data
            .filter((c) => c.isActive)
            .sort(
              (a, b) =>
                new Date(a.createdAt).getTime() -
                new Date(b.createdAt).getTime(),
            )
            .map((c) => ({
              id: c.id,
              name: c.name,
              slug: generateSlug(c.name),
            }));
          setCategories(mapped);
        }

        // Tamanhos
        if (sizeRes.ok) {
          const data: SizeApi[] = await sizeRes.json();
          const mappedSizes: Size[] = data
            .filter((s) => s.isActive)
            .map((s) => ({ id: s.id, name: s.name }));
          setSizes(mappedSizes);
        }

        // Cores
        if (colorRes.ok) {
          const data: ColorApi[] = await colorRes.json();
          const mappedColors: Color[] = data
            .filter((c) => c.isActive)
            .map((c) => ({
              id: c.id,
              name: c.name,
              hex: c.hexCode,
            }));
          setColors(mappedColors);
        }

        // Produtos
        if (productRes.ok) {
          const data: ProductApi[] = await productRes.json();

          const mappedProducts: Product[] = data
            .filter((p) => p.isActive)
            .map((p) => {
              const primaryImage =
                p.images.find((img) => img.isPrimary) ?? p.images[0];

              return {
                id: p.id,
                categoryId: p.categoryId,
                name: p.name,
                price: p.price,
                promotionalPrice: p.promotionalPrice,
                imageUrl: primaryImage?.url ?? null,
                sizeIds: p.sizeIds ?? [],
                colorIds: p.colorIds ?? [],
                createdAt: p.createdAt,
              };
            });

          setProducts(mappedProducts);
        }
      } catch (error) {
        console.error("Erro ao carregar dados da homepage:", error);
      } finally {
        setLoadingSidebar(false);
        setLoadingProducts(false);
      }
    };

    void fetchData();
  }, []);

  // Contagem de produtos por tamanho/cor
  const sizeCounts = useMemo(() => {
    const counts: Record<number, number> = {};
    products.forEach((p) => {
      p.sizeIds.forEach((id) => {
        counts[id] = (counts[id] || 0) + 1;
      });
    });
    return counts;
  }, [products]);

  const colorCounts = useMemo(() => {
    const counts: Record<number, number> = {};
    products.forEach((p) => {
      p.colorIds.forEach((id) => {
        counts[id] = (counts[id] || 0) + 1;
      });
    });
    return counts;
  }, [products]);

  // Toggle seleção de filtros
  const toggleSize = (id: number) => {
    setSelectedSizeIds((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id],
    );
  };

  const toggleColor = (id: number) => {
    setSelectedColorIds((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id],
    );
  };

  const handleResetFilters = () => {
    setSelectedCategoryId("Todos");
    setSelectedPrice("Todas");
    setSelectedSizeIds([]);
    setSelectedColorIds([]);
    setShowAllSizes(false);
    setShowAllColors(false);
    setSortBy("popular");
  };

  // Helpers pra exibir nomes de tamanho/cor do produto
  const getSizeLabel = (product: Product) => {
    const names = product.sizeIds
      .map((id) => sizes.find((s) => s.id === id)?.name)
      .filter(Boolean);
    return names.join(", ");
  };

  const getColorLabel = (product: Product) => {
    const names = product.colorIds
      .map((id) => colors.find((c) => c.id === id)?.name)
      .filter(Boolean);
    return names.join(", ");
  };

  // Opções de categoria para o filtro (igual ProductsPage)
  const categoryOptions: { id: string; label: string }[] = [
    { id: "Todos", label: "Todos" },
    ...categories.map((c) => ({ id: c.id, label: c.name })),
  ];

  // Função para filtrar por faixa de preço
  const matchPriceRange = (product: Product, range: string) => {
    if (range === "Todas") return true;

    const basePrice = product.price;
    const finalPrice =
      product.promotionalPrice && product.promotionalPrice > 0
        ? product.promotionalPrice
        : basePrice;

    switch (range) {
      case "R$ 0-100":
        return finalPrice >= 0 && finalPrice <= 100;
      case "R$ 100-300":
        return finalPrice > 100 && finalPrice <= 300;
      case "R$ 300-500":
        return finalPrice > 300 && finalPrice <= 500;
      case "R$ 500+":
        return finalPrice > 500;
      default:
        return true;
    }
  };

  // Produtos filtrados (categoria, tamanho, cor, preço + ordenação)
  const filteredProducts = useMemo(() => {
    let list = products.filter((p) => {
      const matchCategory =
        selectedCategoryId === "Todos" ||
        p.categoryId === selectedCategoryId;

      const matchSize =
        selectedSizeIds.length === 0 ||
        p.sizeIds.some((id) => selectedSizeIds.includes(id));

      const matchColor =
        selectedColorIds.length === 0 ||
        p.colorIds.some((id) => selectedColorIds.includes(id));

      const matchPrice = matchPriceRange(p, selectedPrice);

      return matchCategory && matchSize && matchColor && matchPrice;
    });

    list = [...list];

    list.sort((a, b) => {
      const aBase = a.price;
      const aFinal =
        a.promotionalPrice && a.promotionalPrice > 0
          ? a.promotionalPrice
          : aBase;

      const bBase = b.price;
      const bFinal =
        b.promotionalPrice && b.promotionalPrice > 0
          ? b.promotionalPrice
          : bBase;

      switch (sortBy) {
        case "newest":
          return (
            new Date(b.createdAt).getTime() -
            new Date(a.createdAt).getTime()
          );
        case "price-low":
          return aFinal - bFinal;
        case "price-high":
          return bFinal - aFinal;
        case "popular":
        default:
          return (
            new Date(b.createdAt).getTime() -
            new Date(a.createdAt).getTime()
          );
      }
    });

    return list;
  }, [
    products,
    selectedCategoryId,
    selectedSizeIds,
    selectedColorIds,
    selectedPrice,
    sortBy,
  ]);

  // Tamanhos/cores visíveis (para "Ver todos")
  const visibleSizes = showAllSizes ? sizes : sizes.slice(0, 7);
  const visibleColors = showAllColors ? colors : colors.slice(0, 7);

  return (
    <div className="min-h-screen flex flex-col">
      <Header />

      <HeroCarousel />

      {/* Main Content */}
      <div className="flex-1 flex">
        {/* Sidebar com filtros (mesmo filtro da ProductsPage) */}
        <aside className="hidden lg:block w-72 bg-muted/30 border-r border-border px-6 py-8 overflow-y-auto">
          <div className="sticky top-24 space-y-6">
            <div className="flex items-center space-x-2">
              <Filter size={20} className="text-foreground" />
              <h3 className="font-medium text-foreground">Filtros</h3>
            </div>

            {/* Categoria */}
            <div>
              <h4 className="font-medium text-foreground mb-3 text-sm">
                Categoria
              </h4>

              {loadingSidebar && categories.length === 0 ? (
                <p className="text-xs text-foreground/60">
                  Carregando categorias...
                </p>
              ) : (
                <div className="space-y-2">
                  {categoryOptions.map((cat) => (
                    <label
                      key={cat.id}
                      className="flex items-center space-x-2 cursor-pointer"
                    >
                      <input
                        type="radio"
                        name="category"
                        value={cat.id}
                        checked={selectedCategoryId === cat.id}
                        onChange={(e) =>
                          setSelectedCategoryId(e.target.value)
                        }
                        className="rounded-sm"
                      />
                      <span className="text-sm text-foreground/70">
                        {cat.label}
                      </span>
                    </label>
                  ))}
                </div>
              )}
            </div>

            {/* Preço */}
            <div>
              <h4 className="font-medium text-foreground mb-3 text-sm">
                Preço
              </h4>
              <div className="space-y-2">
                {PRICE_RANGES.map((range) => (
                  <label
                    key={range}
                    className="flex items-center space-x-2 cursor-pointer"
                  >
                    <input
                      type="radio"
                      name="price"
                      value={range}
                      checked={selectedPrice === range}
                      onChange={(e) => setSelectedPrice(e.target.value)}
                      className="rounded-sm"
                    />
                    <span className="text-sm text-foreground/70">
                      {range}
                    </span>
                  </label>
                ))}
              </div>
            </div>

            {/* Tamanho */}
            <div>
              <h4 className="font-medium text-foreground mb-3 text-sm">
                Tamanho
              </h4>

              {loadingSidebar && sizes.length === 0 ? (
                <p className="text-xs text-foreground/60">
                  Carregando tamanhos...
                </p>
              ) : (
                <div className="space-y-2">
                  {visibleSizes.map((size) => (
                    <label
                      key={size.id}
                      className="flex items-center space-x-2 cursor-pointer"
                    >
                      <input
                        type="checkbox"
                        className="rounded-sm"
                        checked={selectedSizeIds.includes(size.id)}
                        onChange={() => toggleSize(size.id)}
                      />
                      <span className="text-sm text-foreground/70">
                        {size.name}{" "}
                        <span className="text-xs text-foreground/60">
                          ({sizeCounts[size.id] ?? 0})
                        </span>
                      </span>
                    </label>
                  ))}
                </div>
              )}

              {sizes.length > visibleSizes.length && (
                <button
                  type="button"
                  onClick={() => setShowAllSizes(true)}
                  className="mt-3 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-primary/30 text-primary bg-background hover:bg-primary/5 transition-colors"
                >
                  Ver todos
                </button>
              )}

              {showAllSizes && sizes.length > 7 && (
                <button
                  type="button"
                  onClick={() => setShowAllSizes(false)}
                  className="mt-2 ml-1 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-border text-foreground/70 bg-background hover:bg-muted transition-colors"
                >
                  Ver menos
                </button>
              )}
            </div>

            {/* Cor */}
            <div>
              <h4 className="font-medium text-foreground mb-3 text-sm">
                Cor
              </h4>

              {loadingSidebar && colors.length === 0 ? (
                <p className="text-xs text-foreground/60">
                  Carregando cores...
                </p>
              ) : (
                <div className="space-y-2">
                  {visibleColors.map((color) => (
                    <label
                      key={color.id}
                      className="flex items-center space-x-2 cursor-pointer"
                    >
                      <input
                        type="checkbox"
                        className="rounded-sm"
                        checked={selectedColorIds.includes(color.id)}
                        onChange={() => toggleColor(color.id)}
                      />
                      <span className="flex items-center space-x-2 text-sm text-foreground/70">
                        <span
                          className="w-4 h-4 rounded-full border border-border"
                          style={{
                            backgroundColor: color.hex || "#ffffff",
                          }}
                        />
                        <span>
                          {color.name}{" "}
                          <span className="text-xs text-foreground/60">
                            ({colorCounts[color.id] ?? 0})
                          </span>
                        </span>
                      </span>
                    </label>
                  ))}
                </div>
              )}

              {colors.length > visibleColors.length && (
                <button
                  type="button"
                  onClick={() => setShowAllColors(true)}
                  className="mt-3 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-primary/30 text-primary bg-background hover:bg-primary/5 transition-colors"
                >
                  Ver todos
                </button>
              )}

              {showAllColors && colors.length > 7 && (
                <button
                  type="button"
                  onClick={() => setShowAllColors(false)}
                  className="mt-2 ml-1 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-border text-foreground/70 bg-background hover:bg-muted transition-colors"
                >
                  Ver menos
                </button>
              )}
            </div>

            {/* Resetar */}
            <button
              onClick={handleResetFilters}
              className="w-full py-2 border border-border rounded-lg text-sm font-medium text-foreground hover:bg-muted transition-colors"
            >
              Limpar Filtros
            </button>
          </div>
        </aside>

        {/* Main Content Area */}
        <main className="flex-1 px-4 sm:px-6 lg:px-8 py-12">
          <div className="max-w-6xl mx-auto">
            {/* Header Section */}
            <div className="mb-12">
              <p className="text-accent font-medium text-sm uppercase tracking-widest mb-2">
                Catálogo Premium
              </p>
              <h1 className="text-4xl md:text-5xl font-light text-foreground mb-4 text-balance">
                Produtos Personalizados Únicos
              </h1>
              <p className="text-lg text-foreground/70 max-w-2xl">
                Explore nossa coleção exclusiva de produtos premium, cada um
                pronto para ser personalizado conforme sua criatividade.
              </p>
            </div>

            {/* Barra de info + ordenação (mesma lógica da ProductsPage) */}
            <div className="flex flex-col sm:flex-row gap-4 items-start sm:items-center justify-between mb-8">
              <p className="text-sm text-foreground/70">
                {loadingProducts
                  ? "Carregando produtos..."
                  : `Mostrando ${filteredProducts.length} produto${
                      filteredProducts.length === 1 ? "" : "s"
                    }`}
              </p>
              <div className="flex items-center space-x-2">
                <label className="text-sm text-foreground/70">
                  Ordenar:
                </label>
                <select
                  value={sortBy}
                  onChange={(e) =>
                    setSortBy(e.target.value as typeof sortBy)
                  }
                  className="px-3 py-1 border border-border rounded-lg text-sm bg-background text-foreground"
                >
                  <option value="popular">Mais Popular</option>
                  <option value="newest">Mais Novo</option>
                  <option value="price-low">Menor Preço</option>
                  <option value="price-high">Maior Preço</option>
                </select>
              </div>
            </div>

            {/* Products Grid */}
            {loadingProducts && products.length === 0 ? (
              <div className="py-12 text-center text-foreground/60">
                Carregando produtos...
              </div>
            ) : filteredProducts.length === 0 ? (
              <div className="py-12 text-center text-foreground/60">
                Nenhum produto encontrado com os filtros selecionados.
              </div>
            ) : (
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                {filteredProducts.map((product) => {
                  const sizeLabel = getSizeLabel(product);
                  const colorLabel = getColorLabel(product);

                  const basePrice = product.price;
                  const finalPrice =
                    product.promotionalPrice && product.promotionalPrice > 0
                      ? product.promotionalPrice
                      : basePrice;

                  let discountLabel: string | null = null;
                  if (
                    product.promotionalPrice &&
                    product.promotionalPrice > 0 &&
                    product.promotionalPrice < basePrice
                  ) {
                    const discount =
                      100 - Math.round((finalPrice / basePrice) * 100);
                    if (discount > 0) {
                      discountLabel = `-${discount}%`;
                    }
                  }

                  return (
                    <div key={product.id} className="group flex flex-col h-full">
                      <Link
                        href={`/products/${product.id}`}
                        className="flex-1 flex flex-col"
                      >
                        <div className="relative bg-muted rounded-lg overflow-hidden aspect-square mb-4">
                          <img
                            src={product.imageUrl || "/placeholder.svg"}
                            alt={product.name}
                            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                          />
                          {discountLabel && (
                            <div className="absolute top-3 right-3 bg-accent text-accent-foreground px-3 py-1 rounded-full text-xs font-medium">
                              {discountLabel}
                            </div>
                          )}
                        </div>

                        {/* Conteúdo do card alinhado */}
                        <div className="flex-1 flex flex-col">
                          {/* Título com altura mínima para 2 linhas */}
                          <h3 className="font-medium text-foreground group-hover:text-accent transition-colors min-h-[44px]">
                            {product.name}
                          </h3>

                          {/* Label de tamanho/cor SEMPRE renderizado com altura mínima */}
                          <p className="text-xs text-foreground/60 mt-1 min-h-[16px]">
                            {sizeLabel && <span>{sizeLabel}</span>}
                            {sizeLabel && colorLabel && " • "}
                            {colorLabel && <span>{colorLabel}</span>}
                          </p>

                          {/* Preço + rating grudados no rodapé da área de conteúdo */}
                          <div className="mt-3 flex items-center justify-between">
                            <div className="space-y-1">
                              {/* Linha de preço antigo sempre existe, só fica invisível se não tiver desconto */}
                              <p
                                className={`text-sm text-foreground/50 line-through ${
                                  !(
                                    product.promotionalPrice &&
                                    product.promotionalPrice > 0 &&
                                    product.promotionalPrice < basePrice
                                  )
                                    ? "invisible"
                                    : ""
                                }`}
                              >
                                {formatCurrency(basePrice)}
                              </p>
                              <p className="font-medium text-foreground">
                                {formatCurrency(finalPrice)}
                              </p>
                            </div>
                            <div className="flex items-center space-x-1">
                              <Star size={16} className="fill-accent text-accent" />
                              <span className="text-xs text-foreground/70">4.9</span>
                            </div>
                          </div>
                        </div>
                      </Link>

                      {/* Botão sempre alinhado na base do card */}
                      <Link
                        href={`/products/${product.id}`}
                        className="mt-3 w-full flex items-center justify-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
                      >
                        <ShoppingCart size={18} />
                        Comprar
                      </Link>
                    </div>
                  );
                })}
              </div>
            )}

            {/* Load More (placeholder – ainda sem paginação real) */}
            <div className="text-center mt-12">
              <button className="px-8 py-3 border-2 border-primary text-primary rounded-lg font-medium hover:bg-primary/5 transition-colors">
                Carregar Mais Produtos
              </button>
            </div>
          </div>
        </main>
      </div>

      <Footer />
      <WhatsAppButton />
    </div>
  );
}